:root {
  --calculated-value: 100;
  --gapW: 6;
  --gapH: 2;
  --half: 50%;
}
.wrapper {
  position: relative;
  top: 100px;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: absolute;
  .video {
    height: 300px;
    width: 900px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 15px;
    clip-path: var(--clip-path);
  }
}
.wrapper1 {
  --clip-path: xywh(
    0 0 calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--half)) round
      20px 20px 20px 20px
  );

  animation: movewrapper1 5s infinite;
  transition: all ease-in-out 5s;
}
