      0 0 calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--half))
        round 20px 0px 0px 20px
    );
    transform: translateY(0px);
  }
  /* 70% {
    transform: translateY(180px);
  } */
  100% {
